import React from 'react'
import { Stack, Box } from '@mui/material'
import Page from '../../layout/Page'
import { graphicsData } from '../../utils/Constants'
import { Routes, Route, useNavigate } from 'react-router-dom'
import Details from './Details'

const Graphics = () => {

   const navigate = useNavigate()

   return (
      <Page metaTitle="Portfolio" metaContent="This page is for portfolio.">
         <section className="portfolio">
            <div className="container">

               <h2>Graphics</h2>

               <Stack component='main' className='main' sx={{ display: 'grid', gridTemplateColumns: {sm:'repeat(2, 1fr)',md:'repeat(3, 1fr)',lg:'repeat(4, 1fr)'}, gap: 5 }}>
                  {graphicsData.map((item, idx) => (
                     <Box component='figure' className='figures' key={idx}>
                        <img src={item.imgUrl} alt="" />
                        <figcaption>{item.figCap}</figcaption>
                     </Box>
                  ))}
               </Stack>

               { 
                  window.location.pathname === '/portfolio/graphics' ?
                     <>
                     Tools Details [<span onClick={() => navigate('/portfolio/graphics/details')} style={{cursor:'pointer'}}>Click Me!</span>]
                     </>
                  : null
               }

               <Routes>
                  <Route path="details" element={<Details />} />
               </Routes>

            </div>
         </section>
      </Page>
   )

} 
export default Graphics