import React from 'react'
import { NavLink, Link } from 'react-router-dom'
import { menuListData } from '../../utils/Constants'

const Navlist = () => {

   return (
      <ul>
         {menuListData.map((item, idx) => {

            return item.children ? // If nav list has child
               <li className='has-submenu' key={idx}>

                  <Link to={item.url}>{item.name}</Link>

                  <ul className='submenu'>
                     {item.children.map((child, idz) => (
                        <li key={idz}>
                           <NavLink to={child.url} className={(navData) => (navData.isActive ? 'active' : '')}>{child.name}</NavLink>
                        </li>
                     ))}
                  </ul>
               </li>
            : // No child in the list
               <li key={idx}>
                  <NavLink to={item.url} className={(navData) => (navData.isActive ? 'active' : '')}>{item.name}</NavLink>
               </li>

         })}
      </ul>
   )
}
export default Navlist