import React from 'react'
import { Header, Footer } from '../containers'
import { Helmet } from 'react-helmet'

const Page = ({ children, metaTitle, metaContent }) => {
   return (
      <>
      <Helmet>
         <title>{metaTitle} - Website Meta Title</title>
         <meta name="description" content={metaContent} />
      </Helmet>
      <section id="page">
         <Header />
         {children}
         <Footer />
      </section>
      </>
   )
}

export default Page