import React from 'react'
import { Helmet } from 'react-helmet'
import { Link } from 'react-router-dom'
import bigR from '../assets/imgs/big-r.svg'

const Home = () => {

   return (
      <>
      <Helmet>
         <title>Home - Website Meta Title</title>
         <meta name="description" content="This page is created by React" />
      </Helmet>
      <section id="home-page">
         <Link to='/redirect'>
            <img src="/assets/imgs/big-r.svg" alt="" />
            <img src={bigR} alt="RA Web & Creative" />
         </Link>
      </section>
      </>
   )

}
export default Home